<template>
  <div class="exercise-item-card">
    <p class="exercise-text">
      <span v-html="exercise.text"></span>
      <span @click="speak(exercise.text, 'en-US')" class="speaker-icon clickable-text exercise-speaker">üîà</span>
    </p>

    <div class="answer-area">
      <input
        v-if="isTextInputType(exercise.type)"
        type="text"
        v-model="userAnswer"
        :placeholder="exercise.placeholder || 'Escribe tu respuesta aqu√≠'"
        @input="clearFeedbackOnInputChange"
        @keypress.enter="checkThisSpecificAnswer"
        class="answer-input"
        :aria-label="'Respuesta para el ejercicio: ' + exercise.text"
        :class="{ 'input-correct': feedbackType === 'correct', 'input-incorrect': feedbackType === 'incorrect' }"
      >
    </div>

    <div class="feedback-and-review-actions">
      <transition name="fade">
        <span
          v-if="feedbackText"
          :class="['feedback-span', feedbackType]"
          aria-live="polite"
        >
          <template v-if="feedbackType === 'correct'">‚úÖ </template>
          <template v-else-if="feedbackType === 'incorrect'">‚ùå </template>
          {{ feedbackText }}
        </span>
      </transition>
      <button
        @click="checkThisSpecificAnswer"
        class="btn btn-small review-one-btn"
        v-if="!isBeingChecked && userAnswer.trim() !== '' && feedbackType === ''"
      >
        Revisar
      </button>
    </div>

    <transition name="fade">
      <p v-if="showCorrectAnswerAfterCheck && feedbackType === 'incorrect'" class="correct-answer-display">
        <span>‚ÑπÔ∏è Respuesta correcta:</span>
        <strong>{{ exercise.correctAnswer }}</strong>
        <span @click="speak(exercise.correctAnswer, 'en-US')" class="speaker-icon clickable-text answer-speaker">üîà</span>
      </p>
    </transition>
  </div>
</template>

<script setup>
import { ref, computed, toRefs, watch } from 'vue';
import { speakText } from '@/utils/speechUtils';

const emit = defineEmits(['update:answer', 'update:feedback']);
const props = defineProps({
  exercise: { type: Object, required: true },
  isBeingChecked: { type: Boolean, default: false }
});
const { exercise, isBeingChecked } = toRefs(props);

const userAnswer = ref('');
const feedbackText = ref('');
const feedbackType = ref('');
const showCorrectAnswerAfterCheck = ref(false);

// Normaliza la respuesta para comparar (sin espacios y min√∫sculas)
function normalizeAnswer(text) {
  return (text || '').trim().toLowerCase();
}

// Califica la respuesta
function performCheck() {
  const correctAnswerNormalized = normalizeAnswer(exercise.value.correctAnswer);
  const userAnswerNormalized = normalizeAnswer(userAnswer.value);

  let isCorrect = userAnswerNormalized === correctAnswerNormalized;
  // Permite respuesta alternativa si existe
  if (!isCorrect && exercise.value.correctAnswerShort) {
    isCorrect = userAnswerNormalized === normalizeAnswer(exercise.value.correctAnswerShort);
  }

  if (userAnswer.value === '') {
    feedbackText.value = 'Por favor, escribe una respuesta.';
    feedbackType.value = '';
    showCorrectAnswerAfterCheck.value = false;
  } else if (isCorrect) {
    feedbackText.value = '¬°Correcto!';
    feedbackType.value = 'correct';
    showCorrectAnswerAfterCheck.value = false;
  } else {
    feedbackText.value = 'Incorrecto.';
    feedbackType.value = 'incorrect';
    showCorrectAnswerAfterCheck.value = true;
  }
  emit('update:feedback', { id: exercise.value.id, type: feedbackType.value, text: feedbackText.value });
}

function checkThisSpecificAnswer() { performCheck(); }
function clearFeedback() { feedbackText.value = ''; feedbackType.value = ''; showCorrectAnswerAfterCheck.value = false; }
function clearFeedbackOnInputChange() { if (!isBeingChecked.value) clearFeedback(); }

const isTextInputType = (type) => [
  'fill_in_blank',
  'form_sentence_negative',
  'form_sentence_question',
  'fill_in_blank_verb'
].includes(type);

function stripHtml(html) {
  let tmp = document.createElement("DIV");
  tmp.innerHTML = html;
  return tmp.textContent || tmp.innerText || "";
}

function speak(textToSpeak, lang) {
  if (textToSpeak) {
    const cleanText = stripHtml(textToSpeak);
    const textForSpeech = cleanText.replace(/______\s*\([^)]+\)/g, '').trim();
    if (textForSpeech) {
      speakText(textForSpeech, lang);
    }
  }
}

// Si isBeingChecked cambia a true, revisa autom√°ticamente
watch(isBeingChecked, (newVal) => {
  if (newVal) performCheck();
});

const feedbackComputedClass = computed(() => {
  return feedbackType.value === 'correct'
    ? 'feedback-correct'
    : feedbackType.value === 'incorrect'
    ? 'feedback-incorrect'
    : '';
});
</script>

<style scoped>
.exercise-item-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px #0001;
  padding: 1.2em 1.5em;
  margin-bottom: 1.5em;
  transition: box-shadow 0.2s;
}
.exercise-item-card:focus-within {
  box-shadow: 0 4px 16px #4a90e233;
}
.answer-input {
  width: 100%;
  padding: 0.5em;
  border-radius: 6px;
  border: 1.5px solid #bdbdbd;
  margin-bottom: 0.7em;
  font-size: 1em;
  transition: border-color 0.2s;
}
.input-correct {
  border-color: #4caf50 !important;
  background: #e8f5e9;
}
.input-incorrect {
  border-color: #e53935 !important;
  background: #ffebee;
}
.feedback-span {
  display: inline-block;
  margin-right: 1em;
  font-weight: 500;
  font-size: 1em;
  padding: 0.2em 0.7em;
  border-radius: 6px;
  min-width: 90px;
  text-align: center;
  transition: background 0.2s, color 0.2s;
}
.feedback-correct {
  background: #e8f5e9;
  color: #388e3c;
  border: 1.5px solid #4caf50;
}
.feedback-incorrect {
  background: #ffebee;
  color: #c62828;
  border: 1.5px solid #e53935;
}
.btn-small {
  font-size: 0.95em;
  padding: 0.3em 1.1em;
  margin-top: 0.5em;
}
.correct-answer-display {
  margin-top: 0.7em;
  color: #1976d2;
  font-size: 1em;
  background: #e3f2fd;
  border-radius: 6px;
  padding: 0.4em 0.8em;
  display: flex;
  align-items: center;
  gap: 0.5em;
}
.clickable-text {
  cursor: pointer;
}
.speaker-icon {
  font-size: 0.9em;
  margin-left: 8px;
  opacity: 0.7;
  display: inline-block;
  vertical-align: middle;
  transition: opacity 0.2s;
}
.speaker-icon:hover {
  opacity: 1;
}
.exercise-text {
  display: flex;
  align-items: center;
}
.exercise-text > span:first-child {
  flex-grow: 1;
}
.exercise-text > span.exercise-speaker {
  cursor: pointer;
  color: var(--primary-color, #4A90E2);
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter-from, .fade-leave-to {
  opacity: 0;
}
</style>